@use '../../assets/styles/variables';

.notFound {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 1200px;
  margin: 64px auto 187px;

  @media (max-width: variables.$bp-1275) {
    width: 100%;
  }

  img {
    margin-bottom: 54px;
  }

  h1 {
    font-size: 36px;
    font-weight: 500;
    margin-bottom: 22px;
    line-height: 35px;
  }

  p {
    width: 100%;
    max-width: 664px;
    font-size: 20px;
    font-weight: 400;
    line-height: 30px;
    text-align: center;
    margin-bottom: 24px;
  }
}
