@use '../../../assets/styles/variables';

.infoBlock {
  max-width: 1223px;
  margin-inline: auto;
  display: flex;
  flex-wrap: wrap;
  gap: 93px;

  @media (max-width: variables.$bp-1275) {
    flex-direction: column;
    align-items: center;
  }

  .left {
    width: 650px;
    padding-top: 38px;

    @media (max-width: variables.$bp-1275) {
      width: 98%;
      padding-top: 0;
    }

    .title {
      font-size: 32px;
      font-weight: 600;
      line-height: 35px;
      margin-bottom: 31px;
    }

    .text {
      font-size: 16px;
      font-weight: 300;
      line-height: 26px;
      margin-bottom: 4px;
    }
  }

  .right {
    @media (max-width: variables.$bp-1275) {
      width: 100%;
      padding-top: 0;
    }

    .container {
      width: 480px;
      position: relative;

      @media (max-width: variables.$bp-1275) {
        margin: auto;
      }

      @media (max-width: variables.$bp-538) {
        width: 347px;
      }

      .front {
        width: 347px;
        height: 432px;
      }

      .back {
        z-index: -1;
        position: absolute;
        top: 66px;
        left: 241px;
        width: 241px;
        height: 300px;
        background: url('../../../assets/images/svg/wavy-pattern.svg');

        @media (max-width: variables.$bp-538) {
          display: none;
        }

        .image {
          z-index: 0;
        }

        .pattern {
          position: absolute;
          top: 0;
          left: 0;
          z-index: 10;
        }
      }
    }
  }
}
