@use '../../../../assets/styles/variables';

.search {
  position: relative;
  margin-top: 20px;

  .input {
    border: 1px solid variables.$border-grey;
    padding: 12px 42px;
    width: 392px;
    border-radius: 10px;
    font-size: 16px;

    &:focus {
      border: 1px solid rgba(0, 0, 0, 0.2);
    }
  }

  .icon {
    position: absolute;
    top: 13px;
    left: 12px;
    width: 22px;
    height: 22px;
    opacity: 0.3;
  }

  .clearIcon {
    width: 20px;
    height: 20px;
    position: absolute;
    right: 10px;
    top: 14px;
    opacity: 0.3;
    cursor: pointer;

    &:hover {
      opacity: 0.8;
    }
  }
}
